import React from 'react';
import './App.css';

function Plans(props){
    return(
        <>
        <div className="col-10">
        <li className="shadow p-3 py-2 px-1 mb-2">{props.value}</li>
        </div>
        <div className="col-2 px-0">
        <input type="button" className="btn btn-success px-3 mb-1 rt ms-1 mb-1 mt-1" value="X"  onClick={()=>{props.del(props.id)}} ></input>
        </div>
       </>
    );   
    
}

export default Plans;