import React, { Component } from 'react';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import Plans from './Plans';

class App extends Component {
  
  state={
    data:[],
    text:""
}

val=e=>{
       this.setState({text:e.target.value});
       
}

store=()=>{
if(this.state.text!=="")
{
  let a=this.state.text;
  let data=[...this.state.data,a];
  this.setState({data:data,text:""});
}  

}

delete=(delid)=>{
  let newarr=[];
  newarr=this.state.data.filter((elements,i)=>{
      return i!==delid;
    })
    this.setState({data:newarr});
}


render()

  {
    return (
      <div className="mx-auto container-fluid">
      <div className="box shadow-lg p-4 mb-5 rounded my-3">
        <h1 className="text-center fw-bold font-monospace">My Plans!</h1>
        <div className="row mx-auto mt-4">
        <div className="col-sm-9">
        <div className="mb-3">
          <input type="text" className="form-control" value={this.state.text} onChange={this.val} ></input>
            </div>
        </div>
        <div className="col-sm-3 mx-auto">
         <input type="button" className="btn btn-danger px-5 w-100" value="Add"  onClick={this.store} ></input>
        </div>
        </div>
        <ul className='list-unstyled'>
        <div className='row mt-2 p-3'>
         {
          this.state.data.map((value,id)=>{
            return(
                  <Plans value={value} key={id} id={id} del={this.delete} />
            )
          })

         } 
         </div>
         </ul>
       
        
      </div>
      </div>
    );
  }
}

export default App;




